import searchView from '@/components/search/search';
export default {
	data() {
		return {
			screenHeight: 0,
			lists: [],
			items: [],
			index: 0
		}
	},
	components:{searchView},
	onLoad(e) {
		uni.getSystemInfo({
			success: (o) => {
				console.log(o)
				this.screenHeight = o.windowHeight;
			}
		});
		this.getData();
	},
	methods: {
		getData (e) {
			var t = this;
			uni.request({
				url: t.$serverUrl + '/class',
				data: { 
					appkey: t.$appkey,
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret)
						return;
					};
					const data = ret.data.result;
					t.lists = data;
					t.items = data[t.index].list;
				}
			});
		},
		getItem (item,i) {
			this.index = i;
			this.items = item.list;
		},
		goSearch (item) {
			uni.navigateTo({
				url: '../search/search?value=' + item.name + '&classid=' + item.classid
			})
		}
	}
}
